/**
 * 
 * @param  {String} prefix 
 * @return {String}          
 */
const randomString = (prefix) => {
    const str = `${prefix || 'id'}-${Math.random().toString(36).slice(-8)}`;

    // IDHTML
    if (document.getElementById(str)) {
        return randomString(prefix);
    }

    return str;
};

export default (prefix) => {
    return randomString(prefix);
};
